import { createShared, useShared } from "helux";
import React from "react";
import { useEffect } from "react";

const { state: sharedObj } = createShared({ a: 100, b: 2 });

function classHOC(WrapComponent: any) {
  return class Index extends React.Component {
    state = {
      name: "alien",
    };
    componentDidMount(): void {
      console.log("HOC");
    }
    render() {
      return <WrapComponent {...this.props} {...this.state} />;
    }
  };
}

function Index(props: { name: any }) {
  const { name } = props;
  const [state, setState] = useShared(sharedObj);
  useEffect(() => {
    console.log("index");
  }, []);

  return <div>hello,word,my name is {name}</div>;
}

export default classHOC(Index);
